.side-menu {
  height: 100%;
  background: rgba(0, 0, 0, 0.7);
  box-shadow: 1px 0px 7px rgba(0, 0, 0, 0.5);
  position: fixed;
  top: 0;
  left: 0;
  width: 170px;
  z-index: 200;
  justify-content: space-between;
}

.nav{
 justify-content: space-between;
}
.spacer{
  margin-top:50px;
}
.side-menu ul{
	height:100%;
	list-style: none;
	display:flex;
	flex-direction: column;
	justify-content: center;
  padding:0px;
  justify-content: space-between;
  

}

.side-menu li{
	
}

.side-menu a {
	color:gray;
	text-decoration: none;
	font-size: 1.2rem;
}

.side-menu a:hover,
.side-menu a:active{
	color:#ffffff;
}

.logo{
  width: 55px;
  margin-left: 55px;
  margin-top: 25px;
  opacity: 0.8;
}


.wrapper{
vertical-align: middle;
}
h2{
  color:white;
  font-family: roboto;
  font-weight: 300;
  justify-content: left;
  font-size:0.5rem;
  margin-bottom:2px;
  margin-left:50px;

}

span{
  color:#00B292;
  font-family: roboto;
  font-weight: 500;
  justify-content: left;
  font-size:0.9rem;
  margin:0px;

}
@media (max-width: 768px){
  .side-menu{
    display:none;
  }
}
